.form {
    display: inline-flex;
    margin-left:10px;
}
.button {
    display : block
}
.input {
    display : block
}
.paginator {
    margin: 10px;
}
.pageNumber {
    padding: 2px;
    border: 1px solid grey;
    cursor: pointer
}
.selectedPage {
    font-size: 1.25em;
    font-weight: bold;
    margin-right: 5px;
}
.selected {
    font-size: 1.25em;
    font-weight: bold;
    margin-right: 5px;
}
.normal {
    margin-right: 5px;
    cursor: pointer
}
