.ProfileWrapper {
    width: 30vw;
    text-align: left;
    margin-top: 10px;
    display: grid;
    grid-template-areas: "l r";
    grid-template-columns: 5fr 1fr;
}
.AboutMe {
    color:yellow;
}

.ProfileLeftContent {
    grid-area: l;
    text-align: left;
    font-size: 1.25em;
    color:white;
}
.ProfileRightContent {
    grid-area: r;
    position:relative;
    font-size: 1.1em;
    color:yellow;
}
.ProfileRightContent img {
    width: 150px;
    border-radius: 10px;
}
.bigAvatar img {
    height: 300px;
    width:300px;
}
.JobStatus {
    position:absolute;
    top:0;
    bottom:0;
    margin:auto 0;
}
